import createElement from 'jgallery/src/utils/create-element';
import withTooltip from 'jgallery/src/utils/with-tooltip';
const withDownloadButton = (constructor) => class extends constructor {
    constructor(albums, params) {
        super(albums, params);
        const downloadIcon = withTooltip(createElement(`<span class="j-gallery-icon"><i class="fa fa-download"></i></span>`), {
            style: {
                color: params.backgroundColor,
                background: params.textColor,
                transform: 'translateY(-8px)',
            },
            content: 'Downloaden',
        });
        downloadIcon.addEventListener('click', () => window.location.href = `${this.getFullUrl()}?download`);
        this.appendControlsElements([downloadIcon]);
    }
    goToItem(item) {
        this.currentItem = item;
        return super.goToItem(item);
    }
    getFullUrl() {
        return this.currentItem.fullUrl;
    }
};
export default withDownloadButton;
